@import url(https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
.header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.title {
  font-weight: 700;
  font-size: 32px;
  color: #404040;
}

.subtitle {
  color: #8c8c8c;
  padding: 10px;
  font-weight: 500;
}

.searchBar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.hero-search input {
  border: 1px solid white;
  background-color: white;
}

.hero-search input::-webkit-input-placeholder {
  color: #b9b9b9;
  font-weight: 500;
  font-size: 18px;
}

.hero-search input:-ms-input-placeholder {
  color: #b9b9b9;
  font-weight: 500;
  font-size: 18px;
}

.hero-search input::-ms-input-placeholder {
  color: #b9b9b9;
  font-weight: 500;
  font-size: 18px;
}

.hero-search input::placeholder {
  color: #b9b9b9;
  font-weight: 500;
  font-size: 18px;
}

.searchBar {
  margin-top: 30px;

  font-size: 20px;
  font-weight: 500;

  border-radius: 40px;
  padding: 20px 0px;
  padding-left: 80px;
  padding-right: 30px;
  width: 60vw;

  border: 1px solid #fdecec;
  background: url(data:image/png;base64,) no-repeat scroll 24px 23px;
  background-color: #fdecec;
}

.searchBar::-webkit-input-placeholder {
  color: #ff1510;
  font-weight: 500;
  font-size: 18px;
}

.searchBar:-ms-input-placeholder {
  color: #ff1510;
  font-weight: 500;
  font-size: 18px;
}

.searchBar::-ms-input-placeholder {
  color: #ff1510;
  font-weight: 500;
  font-size: 18px;
}

.searchBar::placeholder {
  color: #ff1510;
  font-weight: 500;
  font-size: 18px;
}

.toggle-icon {
  max-width: 50px;
  margin-bottom: -8px;
  margin-left: 8px;
  margin-right: 8px;
  cursor: pointer;
}

.favorite-iconx1 {
  max-width: 15px;
  margin-bottom: -2px;
  cursor: pointer;
}
.favorite-iconx2 {
  max-width: 40px;
  margin-bottom: -2px;
  cursor: pointer;
}

.favorite-outlined-iconx1 {
  max-width: 15px;
  max-height: 14px;
  margin-bottom: -2px;
  cursor: pointer;
}

.favorite-outlined-iconx2 {
  max-width: 40px;
  max-height: 39px;
  margin-bottom: -2px;
  cursor: pointer;
}

.heroesHeader-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.heroes-header-total {
  color: #b9b9b9;
  font-weight: 500;
}

.heroes-header-text-color {
  color: #ff1410a2;
}

.heroes-noun-superhero-icon {
  max-width: 14px;
  margin-bottom: -4px;
  margin-right: 4px;
}

.heroes-header-favorite {
  cursor: pointer;
}

.heroes-header-favorite > img {
  margin-left: 8px;
  margin-right: 8px;
}

.hero-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 25px;
  padding: 0;
}

.hero-list > div {
  width: calc(25% - 15px);
  padding: 10px;
}

.hero-list > div:nth-child(4n + 1) {
  padding-left: 0;
}

.hero-list > div:nth-child(4n) {
  padding-right: 0;
}

.hero-list span:nth-last-child() {
  text-align: end;
}

.hero-list-thumbnail {
  width: 100%;
  height: 225px;
  object-fit: cover;
  cursor: pointer;
  border-bottom: 4px solid red;
}

.hero-list-description {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
}

.hero-list-name {
  font-size: 19px;
  font-weight: 700;
  color: #404040;
}

.hero-list.hero-list-loading div {
  opacity: 0.3;
}

.hero-list-loading::before {
  opacity: 1;
  content: 'Carregando...';
  position: absolute;
  left: 40%;
  bottom: 0%;

  font-size: 50px;
  font-weight: 500;
}

.pagination-container {
  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 32px;
  margin-top: 20px;
}

.pagination-container div:first-child {
  color: #ff1510;
  margin-right: 20px;
  cursor: pointer;
}

.pagination-container div:nth-child(2) {
  border: 1px solid red;
  padding: 10px 20px;
  border-radius: 10px;
}

.pagination-container div:last-child {
  color: #ff1510;
  margin-left: 20px;
  cursor: pointer;
}

.heroes-container {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
}

.hero-footer {
  width: 100%;
  height: 64px;
  background-color: #ff1510;
}

.hero-header {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.hero-header img {
  margin: 0 5%;
}




.hero-title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 100%;
}

.hero-title-container img {
  margin-right: 30px;
}

.hero-title {
  font-size: 32px;
  font-weight: 700;
  color: #404040;
}

.hero-description {
  width: 100%;
  font-size: 22px;
  margin-top: 40px;
  font-weight: 500;
  color: #8c8c8c;
  line-height: 40px;
}

.hero-add-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  margin-top: 40px;
  width: 100%;
  color: #404040;
}

.hero-comics {
  width: 70%;
}
.hero-comics > span:first-child {
  font-weight: 500;
  font-size: 20px;
}
.hero-comics > span:last-child {
  font-weight: 500;
  font-size: 22px;
}
.hero-comics img {
  margin: 10px 10px -6px 0px;
}

.hero-movies {
  width: 30%;
}
.hero-movies > span:first-child {
  font-weight: 500;
  font-size: 20px;
}
.hero-movies > span:last-child {
  font-weight: 500;
  font-size: 22px;
}
.hero-movies img {
  margin: 10px 10px -4px 0px;
}

.hero-rating {
  width: 100%;
  margin-top: 40px;
  color: #404040;

  display: flex;
  align-items: center;
}
.hero-rating > span:first-child {
  font-weight: 500;
  font-size: 20px;
}
.hero-rating > span:last-child {
  margin-left: 20px;
}
.hero-rating img {
  height: 23px;
}

.hero-last-comic {
  width: 100%;
  margin-top: 40px;
  font-size: 20px;
  color: #404040;
}
.hero-last-comic > span:first-child {
  font-weight: 500;
}

.hero-image-container {
  display: flex;
  justify-content: center;
  align-items: center;

  flex-wrap: wrap;
}

.hero-image-container img {
  width: 100%;
  height: -webkit-fill-available;
  max-height: 50vh;
  object-fit: cover;
}

.hero-container {
  min-height: calc(100vh - 120px - 64px);
  display: flex;
  flex-direction: row;
}
.hero-container:after {
  content: '';
  position: absolute;

  font-size: 624px;
  color: white;
  font-weight: 700;
  top: 15%;
  left: 10%;
  z-index: 0;

  white-space: nowrap;
}

.hero-container > .hero-description-container {
  width: 30%;
  padding: 10%;
  z-index: 1;
}

.hero-container > .hero-image-container {
  width: 50%;
  padding: 10% 10% 10% 0;
  max-height: calc(100vh - 200px);
  z-index: 1;
}

.hero-releases-container {
  width: 80%;
  padding: 0 10%;
  z-index: 1;

  display: flex;
  flex-direction: column;
}

.hero-releases-title {
  font-size: 32px;
  font-weight: 700;
  color: #404040;
}

.hero-releases-comics {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 30px 0 100px 0;
}

.hero-releases-comics div {
  width: calc(25% - 20px);

  padding: 10px;
}

.hero-releases-comics div img {
  max-width: calc(100% - 20px);
  margin-bottom: 20px;
}

.hero-releases-comics div span {
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 30px;
}

.hero-page {
  background-color: #e7f6e7;
}

* {
  font-family: 'Work Sans', sans-serif;
}

body {
  margin: 0;
  overflow-x: hidden;
}

